#quote-box {
  display: flex;
  width: 60%;
  height: 250px;
  background-color: aqua;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px;
  border-radius: 20px;
}

#text {
  background-color: greenyellow;
  width: 80%;
  text-align: center;
  margin: 20px 0px 0px;
}

#author {
  text-align: end;
  font-size: 20px;
  color: #2525af;
}

.buttons {
  display: flex;
  justify-content: space-between;
}

#tweet-quote {
  background-color: red;
  width: 50px;
  height: 30px;
  text-align: center;
  padding-top: 10px;
  border-radius: 10%;
}
